import { Icon } from '../../interface';

/**
 * Get the icon name.
 * This helps setting the right icon,
 * both if the consumer only types `icon: string`,
 * and if they use the `Icon` interface for writing the icon name.
 * @param {string | Icon} icon - The icon to retrieve the name from.
 * @returns {string} The icon name or the provided string.
 */
export function getIconName(icon: string | Icon): string {
    if (typeof icon === 'string') {
        return icon;
    }

    return icon.name;
}

/**
 * Get the icon color.
 * This is added because the old way of specifying `iconColor` is
 * deprecated in our components. So consumers should now use the new
 * `Icon` interface instead. But our components must still support the
 * old way of writing `iconColor: string`.
 * @param {string | Icon} icon - The icon to retrieve the color from.
 * @param {string} [iconColor] - The color to use when the deprecated `iconColor` is used.
 * @returns {string} The icon color or the provided color string.
 */
export function getIconColor(icon: string | Icon, iconColor?: string): string {
    if (typeof icon === 'string') {
        return iconColor;
    }

    return icon.color;
}
